import React, { Component, Fragment } from 'react';

import Skills from './Skills';

import classes from './OtherSkills.module.css';

import data from '../../data.json';

class OtherSkills extends Component {
    render() {
        return (
            <Fragment>
                <h5 style={{ margin: "0 0 10px 0" }}>Other Skills</h5>
                <div className="other-skills">
                    <div className={classes['other-tech']}>
                        <Skills source="data:image/png;base64,"
                            skillName="Github" />   
                        {data.pages.about.otherSkills.map((item) => <Skills key={item.id} source={item.source} skillName={item.skillName} />)}
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default OtherSkills;